$main-color: #095830
$light-color: #1A893A
$main-grad: linear-gradient(180deg, #1A893A 0%, #095830 100%)
body
  margin: 0
  font-family: "FuturaPT"
  font-weight: 400
h1,h2,h3,h4,h5,h6,p
  margin: 0
  padding: 0
.container
  width: 1280px
  margin: 0 auto 0 auto
@media (max-width: 1300px)
  .container
    width: 1170px
@media (max-width: 1200px)
  .container
    width: 960px
@media (max-width: 992px)
  .container
    width: 720px
@media (max-width: 768px)
  .container
    width: 540px
@media (max-width: 576px)
  .container
    width: 100%
    padding: 0 10px 0 10px
input, button
  cursor: pointer
input:focus
  border: 1px solid $main-color
.current-item
  color: $light-color
.has-submenu
  position: relative
  &::after
    content: ''
    background: url('../img/sprite.svg#has-submenu')
    width: 8px
    height: 8px
    position: absolute
    top: 50%
    right: -16px
    transition: .2s
    margin-top: -4px
@media (min-width: 992px)
  .has-submenu:hover
    &::after
      transition: .2s
      transform: rotate(180deg)
      